<template>
<div>
    <div style="background:#eee;padding: 5px">
        <Card :bordered="false">
            <div slot="title" class="detail-title">
                <img src="../assets/image/btc.png" alt="">
                <h3>比特币</h3>
                <h3>￥47,534</h3>
                <span>2.34%</span>
            </div>
            <Row class="detail-num">
                <Col span="13">
                    <span>24H交易排名：1</span>
                    <span>24H交易量：24,682,788,480</span>
                    <span>总发行量：21,000,000</span>
                    <span>流通量：17,188,987</span>
                </Col>
                <Col span="11" class="detail-num-right">
                    <span>24H最高：￥51,516</span>
                    <span>24H最低：￥50,329</span>
                    <span>流通率：81.85%</span>
                    <span>换手率：2.81%</span>
                </Col>
            </Row>
        </Card>
    </div>
    <Menu mode="horizontal" :theme="theme1" active-name="11" class="menu" @on-select="toggle">
        <MenuItem name="11" class="detail-nav" >
            <Icon type="ios-paper" />
            基本信息
        </MenuItem>
        <MenuItem name="12" class="detail-nav" @click="toggleR">
            <Icon type="ios-people" />
            提醒设置
        </MenuItem>
    </Menu>
    <div style="background:#eee;padding: 5px;padding-top: 3px;">
        <!-- 基本信息 -->
        <div class="detail-first" v-if="left">
            <div class="detail-main">
                <h4>英文名</h4><span>Bitcoin</span>
            </div>
            <hr>
            <div class="detail-main">
                <h4>中文名</h4><span>比特币</span>
            </div>
            <hr>
            <div class="detail-main">
                <h4>简称</h4><span>BTC</span>
            </div>
            <hr>
            <div class="detail-main">
                <h4>发布时间</h4><span>2008-11-01</span>
            </div>
            <hr>
            <div class="detail-main">
                <h4>白皮书</h4><span>白皮书</span>
            </div>
            <hr>
            <div class="detail-main">
                <h4>网站</h4><span>网站1 网站2</span>
            </div>
            <hr>
            <div class="detail-main">
                <h4>区块站</h4><span>区块站1 区块站2 区块站3</span>
            </div>
            <hr>
            <Button type="primary" long class="bottom-button" size="large">点击订阅</Button>
        </div>
        <!-- 提醒设置 -->
       <div class="detail-second" v-if="!left">
            <div class="detail-main">
                <h4>按价格提醒</h4><i-switch class="switch" @on-change="select" />
            </div>
        <hr>
            <div class="select" v-if="more">  
                <div class="detail-main">
                <h4>高于</h4>
                <i-switch size="small" class="switch-small" @on-change="highLess" v-model="higher"/>
                </div>
                <hr>
                <div class="detail-main">
                <h4>低于</h4>
                 <i-switch size="small" class="switch-small" @on-change="lessHigh" v-model="less"/>
                </div>
                <hr>
                <div class="detail-main">
                <h4>指点价格</h4><Input class="input" v-model="value" placeholder="¥输入价格" clearable style="width: 100px" />
                </div>
                <hr>
            </div>
            
            <div class="detail-main">
                <h4>按涨幅提醒</h4><i-switch class="switch" @on-change="selectS" />
            </div>
            <div class="select" v-if="moreS"> 
                <hr> 
                <div class="detail-main">
                <h4>高于</h4>
                <i-switch size="small" class="switch-small" @on-change="upDown" v-model="up"/>
                </div>
                <hr>
                <div class="detail-main">
                <h4>低于</h4>
                 <i-switch size="small" class="switch-small" @on-change="downUp" v-model="down"/>
                </div>
                <hr>
                <div class="detail-main">
                <h4>指点价格</h4><Input class="input" v-model="value" placeholder="%输入涨幅" clearable style="width: 100px" />
                </div>
            </div>
                <Button type="primary" class="bottom-button" size="large">点击保存</Button>
            
        </div>
    </div>
</div>
</template>
<script>
export default {
  name: 'detail',
  data () {
    return {
        theme1: 'light',
        left:true,
        more:false,
        moreS:false,
        higher:true,
        less:false,
        up:true,
        down:false,
        value:'',
    }
  },
  methods:{
      toggle:function(name){
         console.log(name);
         if(name==11){
            this.left=true;}
         else{
            this.left=false;
         };
        //   this.left=true;
      },

      select:function(status){
          if(status==true){
            this.more=true;
          }else{
            this.more=false;
          } 
      },
      selectS:function(status){
          if(status==true){
            this.moreS=true;
          }else{
            this.moreS=false;
          } 
      },
      highLess:function(status){
          if(status==true){
            this.less=false;
          }else{
            this.less=true;
          } 
      },
      lessHigh:function(status){
          if(status==true){
            this.higher=false;
          }else{
            this.higher=true;
          } 
      },
      upDown:function(status){
          if(status==true){
            this.down=false;
          }else{
            this.down=true;
          } 
      },
      downUp:function(status){
          if(status==true){
            this.up=false;
          }else{
            this.up=true;
          } 
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.detail-title{
    height: 25px;
}
.detail-main{
    height: 40px;
    padding-bottom: 0;
}
img{
    width: 25px;
    height: 25px;
    display: inline-block;
    float: left;
}
h3{
    display: inline-block;
    line-height: 25px;
    margin-left: 8px;
}
.detail-main h4{
    display: inline;
    line-height: 40px;
    font-size: 15px;
}
.detail-main span{
    line-height: 35px;
    float: right;
    font-size: 15px;
}
.detail-title span{
    width: 50px;
    height: 25px;
    display: inline-block;
    background-color: red;
    font-size: 15px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    line-height: 25px; 
    float: right;
}
.detail-num span{
    font-size: 13px;
    display: block;
    line-height: 23px;
}
.detail-num-right span{
    margin-left: 15px;
}
.menu {
    height: 40px;
    line-height: 35px;
}
.detail-nav{
    width: 50%;
    text-align: center;
    background-color: #eee;
    
}
.detail-first, .detail-second{
    padding: 5px 16px;
    background-color: #fff;
    border-radius: 3px;
}
.detail-main .input{
    float: right;
    margin-top: 5px;
}
hr{
    height: 2px;
    background-color:#eee;
    border: none;
}
.switch{
    margin-top: 8px;
}
.switch-small{
    margin-top: 12px;
}

.bottom-button{
    display: block;
    margin: 10px auto;
    width: 80%;
}
</style>



