import Vue from 'vue'
import Router from 'vue-router'
import indexcoin from '../components/indexcoin'
import choosed from '../components/choosed'
import detail from '../components/detail'

Vue.use(Router)

export default new Router({
  linkActiveClass: '',
  linkExactActiveClass: '',
  routes: [
    {
      path: '/',
      name: 'indexcoin',
      component:indexcoin
    },
    {
      path: '/choosed',
      name: 'choosed',
      component:choosed
    },
    {
      path:'/detail',
      name:'detail',
      component:detail
    }
  ]
})



