        </router-link>
        </MenuItem>
        
        
        <MenuItem name="2" class="nav">
        <router-link to="/choosed" exact>
            <Icon type="ios-stats" />
            自选行情
        </router-link>
        </MenuItem>
        
    </Menu>
   
    </div>
</template>

<script>
export default {
  name: 'coinnav',
  data () {
    return {
        theme1: 'light'
        // light、dark、primary
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav-top{
    height: 50px;
    line-height: 50px;
}
.nav{
  width: 50%;
  text-align: center;
  font-size: 18px;
}

</style>



